<script>
  export default {
    data(){
      return{
        userInfo:{
          name:"winna丽君君君",
          img:"https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2774616704,1367368457&fm=26&gp=0.jpg",
          job:"前端工程师"
        }
      }
