    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style scoped lang="less">
  .el-header{
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    background: #fbfbfb;
    border-bottom: 1px solid #f3f3f3;
  }
  .el-aside{
    z-index: 999;
    position: fixed;
    top: 0;
    left:0;
    height: 100%;
    background: #2f4050;

  }
  .el-main{
    min-height: 2000px;
    padding: 60px 0 0 240px;

  }
  .el-menu{
    border-right: none;
  }
  .user{
    padding: 30px 0;
  }
  .user span.headimg{
    display: block;
    width: 60px;
    height: 60px;
    margin: 5px auto;
    border-radius: 60px;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .user span.name,.user span.job{
    display: block;
    text-align: center;
    color: #6a7682;
  }
  .user span.name{
    margin: 10px 0 5px 0;
    font-size: 18px;
    color: #c2d3e2;
  }
</style>



